/* eslint-disable */
<template>
  <div>
    <el-row>
      <el-col :span="8">
        <el-card class="box-card">
          <div class="user">
            <img src="../assets/images/user.png">
            <div class="userinfo">
              <p class="name">admin</p>
              <p class="access">超级管理员</p>
            </div>
          </div>
          <div class="login-info">
            <p>登陆时间 <span>2022-12-02</span></p>
            <p>登陆地点 <span>广州</span></p>
          </div>
        </el-card>
        <el-card>
          <el-table
            :data="tableData"
            :stripe="true"
            style="width: 100%"
          >
            <el-table-column
              v-for="(val,key) in tableLabel"
              :key="key"
              :prop='key'
              :label='val'
            />
          </el-table>
        </el-card>
      </el-col>
      <el-col
        :span="16"
        style="padding-left: 10px;"
      >
        <div class="num">
          <el-card
            v-for="item in countData"
            :key="item.name"
            :body-style="{display:'flex', padding:'10px'}"
          >
            <i
              class='icon'
              :class='`el-icon-${item.icon}`'
              :style="{background:item.color}"
            >
            </i>
            <div class="detail">
              <p class="price">￥{{ item.value }}</p>
              <p class="desc">{{ item.name }}</p>
            </div>
          </el-card>
        </div>
        <el-card style="height: 260px;">
          <!-- 折线图 -->
          <div
            ref="echarts0"
            style="height: 260px;"
          ></div>
        </el-card>
        <div class="graph">
          <el-card style="height: 230px;">
            <!-- 柱状图 -->
            <div
              ref="echarts1"
              style="height: 230px;"
            ></div>
          </el-card>
          <el-card style="height: 230px;">
            <!-- 饼状图 -->
            <div
              ref="echarts2"
              style="height: 220px;"
            ></div>
          </el-card>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
// @ is an alias to /src
import { getData } from '../api'
import * as echarts from 'echarts'
export default {
  data () {
    return {
      tableData: [],
      tableLabel: {
        name: '课程',
        todayBuy: '今日购买',
        monthBuy: '本月购买',
        totalBuy: '总购买'
      },
      countData: [
        {
          name: '今日支付订单',
          value: 1234,
          icon: 'success',
          color: '#2ec7c9'
        },
        {
          name: '今日收藏订单',
          value: 210,
          icon: 'star-on',
          color: '#ffb980'
        },
        {
          name: '今日未支付订单',
          value: 1234,
          icon: 's-goods',
          color: '#5ab1ef'
        },
        {
          name: '本月支付订单',
          value: 1234,
          icon: 'success',
          color: '#2ec7c9'
        },
        {
          name: '本月收藏订单',
          value: 210,
          icon: 'star-on',
          color: '#ffb980'
        },
        {
          name: '本月未支付订单',
          value: 1234,
          icon: 's-goods',
          color: '#5ab1ef'
        }
      ]
    }
  },
  mounted () {
    getData().then(({ data }) => {
      // console.log(data);
      const { tableData } = data.data
      const { orderData, userData, videoData } = data.data
      this.tableData = tableData
      // console.log(typeof (tableData[0]));
      // console.log(tableData);
      // console.log(videoData);

      // -------折线图
      // 基于准备好的dom，初始化echarts实例
      var echarts0 = echarts.init(this.$refs.echarts0);
      var echarts0option = {}

      // 获取所有品牌名方法1
      var xAxisName = Object.keys(orderData.data[0])
      // 获取所有品牌名方法2
      // var xAxisName = []
      // for (var i in tableData) {
      //   console.log(i);
      //   xAxisName.push(tableData[i].name)
      // }

      echarts0option.xAxis = { data: xAxisName }
      echarts0option.yAxis = {}
      echarts0option.legend = { data: xAxisName }
      echarts0option.series = []
      // tooltip放上去就显示数值
      echarts0option.tooltip = {}
      xAxisName.forEach(key => {
        echarts0option.series.push({
          name: key,
          type: 'line',
          data: orderData.data.map(item => item[key])
        })
      })
      // console.log(echarts0option.series);
      echarts0.setOption(echarts0option)

      // -------柱状图
      var echarts1 = echarts.init(this.$refs.echarts1);
      var echarts1option = {
        legend: {
          // 图例文字颜色
          textStyle: {
            color: '#333',
          }
        },
        grid: {
          left: '20%',
        },
        // 提示框
        tooltip: {
          trigger: 'axis',
        },
        xAxis: {
          type: 'category', // 类目轴
          data: userData.map(item => item.date),
          axisLine: {
            lineStyle: {
              color: '#17b3a3',
            },
          },
          axisLabel: {
            interval: 0,
            color: '#333',
          },
        },
        yAxis: [
          {
            type: "value",
            axisLine: {
              lineStyle: {
                color: '#17b3a3',
              },
            },
          },
        ],
        color: ['#2ec7c9', '#b6a2de'],
        series: [
          {
            name: '新增用户',
            data: userData.map(item => item.new),
            type: 'bar'
          },
          {
            name: '活跃用户',
            data: userData.map(item => item.active),
            type: 'bar'
          }
        ],
      }
      echarts1.setOption(echarts1option)

      // -------饼状图
      var echarts2 = echarts.init(this.$refs.echarts2);
      var echarts2option = {
        tooltip: {
          trigger: "item",
        },
        color: [
          "#0f78f4",
          "#dd536b",
          "#9462e5",
          "#a6a6a6",
          "#e1bb22",
          "#39c362",
          "#3ed1cf",
        ],
        series: [{
          data: videoData,
          type: 'pie'
        }],
      }
      echarts2.setOption(echarts2option)


    })
  },
  name: 'HomeView',
  components: {
  }
}
</script>

