<template>
  <div class="manage">
    <el-dialog
      title="提示"
      :visible.sync="dialogVisible"
      width="50%"
      :before-close="handleClose"
    >
      <!-- 表单信息 -->
      <el-form
        ref="form"
        :model="form"
        label-width="80px"
        :inline="true"
        :rules="rules"
      >
        <el-form-item
          label="姓名"
          prop="name"
        >
          <el-input
            placeholder="请输入姓名"
            v-model="form.name"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="年龄"
          prop="age"
        >
          <el-input
            placeholder="请输入年龄"
            v-model="form.age"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="性别"
          prop="sex"
        >
          <el-select
            v-model="form.sex"
            placeholder="请选择"
          >
            <el-option
              label="男"
              :value="0"
            ></el-option>
            <el-option
              label="女"
              :value="1"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label="出生日期"
          prop="birth"
        >
          <el-date-picker
            v-model="form.birth"
            type="date"
            placeholder="选择日期"
            value-format="yyyy-MM-DD"
          ></el-date-picker>
        </el-form-item>
        <el-form-item
          label="住址"
          prop="addr"
        >
          <el-input
            placeholder="请输入住址"
            v-model="form.addr"
          ></el-input>
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="cancel">取 消</el-button>
        <el-button
          type="primary"
          @click="submit"
        >确 定</el-button>
      </span>
    </el-dialog>
    <div class="manage-head">
      <el-button
        @click="handleAdd"
        type="primary"
      >
        + 新增
      </el-button>
      <!-- 搜索 -->
      <el-form
        :model="userForm"
        :inline="true"
      >
        <el-form-item>
          <el-input
            placeholder="请输入姓名"
            v-model="userForm.name"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button
            type="primary"
            @click="searchSubmit"
          >查 询</el-button>
        </el-form-item>

      </el-form>
    </div>
    <div class="common-table">
      <el-table
        height="90%"
        :data="tableData"
        stripe
        style="width: 100%;"
      >
        <el-table-column
          prop="name"
          label="姓名"
        >
        </el-table-column>
        <el-table-column
          prop="sex"
          label="性别"
        >
          <template slot-scope="scope">
            <span>{{ scope.row.sex == 0? '男':'女' }}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="age"
          label="年龄"
        >
        </el-table-column>
        <el-table-column
          prop="birth"
          label="出生日期"
        >
        </el-table-column>
        <el-table-column
          prop="addr"
          label="地址"
        >
        </el-table-column>
        <el-table-column
          prop="edit"
          label="操作"
        >
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="handleEdit(scope.row)"
            >编辑</el-button>
            <el-button
              size="mini"
              @click="handleDel(scope.row)"
              type="danger"
            >删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="pager">
        <el-pagination
          layout="prev, pager, next"
          :total="total"
          @current-change="handlePage"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { getUser, addUser, editUser, delUser } from '../api'
export default {
  data () {
    return {
      dialogVisible: false,
      form: {
        name: '',
        addr: '',
        birth: '',
        age: '',
        sex: ''
      },
      rules: {
        name: [
          { required: true, message: '请输入姓名', trigger: 'blur' }
        ],
        age: [
          { required: true, message: '请输入年龄', trigger: 'blur' }
        ],
        addr: [
          { required: true, message: '请输入住址', trigger: 'blur' }
        ],
        birth: [
          { required: true, message: '请输入出生日期', trigger: 'blur' }
        ],
        sex: [
          { required: true, message: '请输入性别', trigger: 'blur' }
        ]
      },
      tableData: [],
      addOrEdit: 0,  // 0新增，1编辑
      total: 0,  // 总条数
      pageData: {
        page: 1,
        limit: 10
      },
      userForm: {
        name: ''
      }
    }
  },
  methods: {
    // 提交
    submit () {
      // 验证表单
      this.$refs.form.validate((valid) => {
        // console.log(valid);
        if (valid) {
          // addOrEdit=0,新增
          if (this.addOrEdit === 0) {
            addUser(this.form).then(() => {
              // 刷新列表
              this.getList()
            })
          }
          // addOrEdit=1,编辑
          else {
            editUser(this.form).then(() => {
              // 刷新列表
              this.getList()
            })
          }
          this.handleClose()
        }
      })
    },
    // 关闭、清空表单
    handleClose () {
      this.dialogVisible = false
      this.$refs.form.resetFields();  // 清空表单
    },
    // 取消
    cancel () {
      this.handleClose()
    },
    // 编辑
    handleEdit (row) {
      console.log(row, '编辑');
      this.addOrEdit = 1
      this.dialogVisible = true
      // 深拷贝
      this.form = JSON.parse(JSON.stringify(row))
    },
    // 删除
    handleDel (row) {
      // console.log(row, '删除');
      this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        delUser({ id: row.id }).then(() => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          this.getList()
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    // 新增
    handleAdd () {
      this.dialogVisible = true
      this.addOrEdit = 0
    },
    // 刷新列表
    getList () {
      getUser({ params: { ...this.pageData, ...this.userForm } }).then(({ data }) => {
        // console.log(data, 'dd');
        this.tableData = data.list
        this.total = data.count || 0
      })
    },
    // 点击某页，val是第几页
    handlePage (val) {
      // console.log(val);
      this.pageData.page = val
      this.getList()
    },
    // 查询
    searchSubmit () {
      this.getList()
    }
  },
  mounted () {
    this.getList()
  }
}
</script>

<style lang="less" scoped>
.manage {
  height: 90%;
  .manage-head {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  .common-table {
    position: relative;
    height: calc(100% - 62px);
    .pager {
      position: absolute;
      bottom: 0;
      right: 20px;
    }
  }
}
</style>
