<template>
  <div class="header-container">
    <div class="l-content">
      <el-radio-group style="margin-bottom: 20px;">
      </el-radio-group>
      <el-button
        icon="el-icon-menu"
        size="mini"
        @click="handleCollapse"
        style="margin-right: 20px;"
      ></el-button>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item
          v-for="item in tags"
          :key="item.path"
        >
          <span @click="toPath(item)">{{item.label}}</span>
        </el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="r-content">
      <el-dropdown @command="handleClick">
        <span class="el-dropdown-link">
          <img
            class="user"
            src="../assets/images/user.png"
            alt=""
          >
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>个人中心</el-dropdown-item>
          <el-dropdown-item command="logout">退出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import Cookies from 'js-cookie'
export default {
  data () {
    return {}
  },
  methods: {
    handleCollapse () {
      this.$store.commit('collapseMenu')
    },
    // 解决多次点击跳转同一页面出现的bug
    toPath (item) {
      // console.log(item.path, '到达')  // 到达的路由
      // console.log(this.$route.path, '出发')  // 出发的路由
      if (this.$route.path !== item.path && !(this.$route.path === '/home' && (item.path === '/'))) {
        this.$router.push(item.path)
      }
      // :to="{ path: item.path }"
    },
    // 登出
    handleClick (command) {
      if (command === 'logout') {
        // Cookies.remove('token')
        // Cookies.remove('menu')
        sessionStorage.clear()
        this.$router.push('/login')
        this.$router.replace({ path: '/login' });
        location.reload();
        // this.$router.addRoute(this.$router)
      }
    }
  },
  computed: {
    ...mapState({
      tags: state => state.tab.tabList
    })
  },
  mounted () {
    // console.log(this.tags, 'tt');
  }
}
</script>

<style lang="less" scoped>
.header-container {
  padding: 0 20px;
  background-color: #333;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  .text {
    color: #fff;
    font-size: 140px;
    margin-left: 10px;
  }
  .r-content {
    .user {
      width: 40px;
      height: 40px;
      border-radius: 50%;
    }
  }
  .l-content {
    display: flex;
    align-items: center;
    /deep/.el-breadcrumb__item {
      .el-breadcrumb__inner {
        font-weight: normal;
        &.is-link {
          color: #eaabab;
        }
      }
      &:last-child {
        .el-breadcrumb__inner {
          color: #fff;
        }
      }
    }
  }
}
</style>
