import router from '@/router'
import Cookies from 'js-cookie'
export default {
  state: {
    isCollapse: false, // 控制左侧展开收起
    // 面包屑数据
    tabList: [
      {
        path: '/',
        name: 'home',
        label: '首页',
        icon: 's-home',
        url: 'Home/Home'
      }
    ],
    menu: []
  }, // 控制菜单收起
  mutations: {
    collapseMenu(state) {
      state.isCollapse = !state.isCollapse
    },
    //更新面包屑数据
    selectMenu(state, val) {
      // console.log(val)
      if (val.name !== 'home') {
        const index = state.tabList.findIndex((item) => item.name === val.name)
        if (index === -1) {
          state.tabList.push(val)
        }
      }
    },
    // 删除tag
    closeTag(state, index) {
      // 删除index位置的数据
      state.tabList.splice(index, 1)
    },
    // 设置menu
    setMenu(state, val) {
      state.menu = val
      // Cookies.set('menu', JSON.stringify(val))
      sessionStorage.setItem('menu', JSON.stringify(val))
      // console.log(Cookies.get('menu'), 'menu')
    },
    // 动态加载路由
    addRouter(state, router) {
      // 判断缓存中是否有数据
      // if (!Cookies.get('menu')) return
      if (!sessionStorage.getItem('menu')) return
      // const menu = JSON.parse(Cookies.get('menu'))
      const menu = JSON.parse(sessionStorage.getItem('menu'))
      state.menu = menu
      const menuArray = []
      var url
      menu.forEach((item) => {
        // 有子菜单
        if (item.children) {
          item.children = item.children.map((inItem) => {
            // url = '../views/' + inItem.url
            // console.log(url, '有子菜单', typeof url)
            // url = '../views/MallView.vue'
            inItem.component = () => import(`@/views/${inItem.url}`)
            return inItem
          })
          menuArray.push(...item.children)
        }
        //没有子菜单
        else {
          // url = '../views/' + item.url
          // console.log(url, '没有子菜单')
          // url = '../views/MallView.vue'
          item.component = () => import(`@/views/${item.url}`)
          menuArray.push(item)
        }
      })
      console.log(menuArray, 'menu')
      menuArray.forEach((item) => {
        router.addRoute('main', item)
      })
      // console.log(router.getRoutes(), 'all')
    },
    // 登出删除路由
    removeRouter(state) {
      // state.menu = []
      // router.removeRoute('main')
      // Cookies.remove('menu')
      // console.log(state.menu, 'state.menu')
    }
  }
}
