<template>
  <div>
    <el-menu
      default-active="1-4-1"
      class="el-menu-vertical-demo"
      @open="handleOpen"
      @close="handleClose"
      :collapse="isCollapse"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b"
    >
      <h3>{{isCollapse?'后台':'后台管理'}}</h3>
      <el-menu-item
        @click="clickMenu(item)"
        v-for="item in nochildren"
        :key="item.name"
        :index="item.name"
      >
        <i :class="`el-icon-${item.icon}`"></i>
        <span slot="title">{{item.label}}</span>
      </el-menu-item>
      <el-submenu
        v-for="item in haschildren"
        :key="item.label"
        :index="item.label"
      >
        <template slot="title">
          <i :class="`el-icon-${item.icon}`"></i>
          <span slot="title">{{item.label}}</span>
        </template>
        <el-menu-item-group
          v-for="subitem in item.children"
          :key="subitem.name"
        >
          <el-menu-item
            @click="clickMenu(subitem)"
            :index="subitem.name"
          >{{subitem.label}}</el-menu-item>
        </el-menu-item-group>
      </el-submenu>

    </el-menu>
  </div>
</template>

<style lang="less" scoped>
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: 400px;
}
.el-menu {
  height: 100vh;
  h3 {
    color: #fff;
    text-align: center;
    line-height: 48px;
    font-size: 16px;
    font-weight: 400px;
  }
}
</style>

<script>
import Cookies from 'js-cookie'
export default {
  data () {
    return {}
  },
  methods: {
    handleOpen (key, keyPath) {
      // console.log(key, keyPath)
    },
    handleClose (key, keyPath) {
      // console.log(key, keyPath)
    },
    clickMenu (item) {
      // console.log(item.path)  // 到达的路由
      // console.log(this.$route.path)  // 出发的路由
      if (this.$route.path !== item.path && !(this.$route.path === '/home' && (item.path === '/'))) {
        this.$router.push(item.path)
      }
      // console.log(item);
      this.$store.commit('selectMenu', item)
    }
  },
  computed: {
    // 没有子菜单
    nochildren () {
      return this.menuData.filter(item => !item.children)
    },
    // 有子菜单
    haschildren () {
      return this.menuData.filter(item => item.children)
    },
    isCollapse () {
      return this.$store.state.tab.isCollapse
    },
    menuData () {
      // return JSON.parse(Cookies.get('menu')) || this.$store.state.tab.menu
      return JSON.parse(sessionStorage.getItem('menu')) || this.$store.state.tab.menu
    }
  }
}
</script>

<style lang="less" scoped>
.el-menu {
  border-right: 0;
}
</style>
